
.tela {
    display: grid;
    grid-template-rows: auto 1fr;
    transition: opacity 1s ease; 
}

.titulo {
    color: #4CA346;
    text-align: center;
    font-feature-settings: 'clig' off, 'liga' off;
    font-family: Signika;
    font-size: 25px;
    font-style: normal;
    font-weight: 600;
    line-height: 35px;
    letter-spacing: -0.24px;
    margin-top: 63px;
    opacity: 0.8; 
}

.titulo-tela2{
    color: #4CA346;
    text-align: center;
    font-feature-settings: 'clig' off, 'liga' off;
    font-family: Signika;
    font-size: 25px;
    font-style: normal;
    font-weight: 600;
    line-height: 35px;
    letter-spacing: -0.24px;
    margin-top: 30px;
    opacity: 0.8;  
}

