.texto-descritivo-container {
    flex-shrink: 0;
    display: flex;
    justify-content: center;
    align-items: center;
    margin: auto;
    max-width: 277px;
    margin-top: -12px;
  }



  .texto-descritivo-tela2-container {
    flex-shrink: 0;
    display: flex;
    justify-content: center;
    align-items: center;
    margin: auto;
    max-width: 277px;
    margin-top: -20px;
  }

  .texto-descritivo-tela2{
    color: rgba(0, 0, 0, 0.45);
    text-align: justify;
    font-feature-settings: 'clig' off, 'liga' off;
    font-size: 17px;
    font-style: normal;
    font-weight: 400;
    line-height: 23.8px; 
    letter-spacing: -0.24px;
  }
  
  
.texto-descritivo{
color: rgba(0, 0, 0, 0.45);
text-align: justify;
font-feature-settings: 'clig' off, 'liga' off;
font-size: 17px;
font-style: normal;
font-weight: 400;
line-height: 23.8px; 
letter-spacing: -0.24px;
  }

  .botao-iniciar {
    width: 290px;
    height: 72px;
    flex-shrink: 0;
    border-radius: 24px;
    background: var(--verde);
    color: #FFF;
    font-family: Signika;
    font-size: 25px;
    font-style: normal;
    font-weight: 600;
    line-height: 72px; 
    letter-spacing: 1.25px;
    text-align: center;
    margin-top: 26px; 
    margin-left: auto;
    margin-right: auto;
    text-decoration: none;
    cursor: pointer;
}

.texto-descritivo span{
    color: var(--verde)
}


.texto-descritivo-tela2-container span{
    color: var(--verde)
}
.frase-azul-container{
    display: flex;
    justify-content: center;
    align-items: center;
}

.frase-azul-tela-2container{
    display: flex;
    justify-content: center;
    align-items: center;
    margin-top: -10px; 
}

.frase2-azul-container{
    display: flex;
    justify-content: center;
    align-items: center;
    margin-top: -40px;
}

.frase-azul {
    color: rgba(27, 83, 96, 0.88);
    text-align: center;
    font-feature-settings: 'clig' off, 'liga' off;
    font-family: Signika;
    font-size: 16px;
    font-style: normal;
    font-weight: 700;
    line-height: 23.8px; 
    letter-spacing: -0.24px;
}

.slide-indicators-container {
    display: flex;
    justify-content: center;
    align-items: center;
    margin-top: 20px; 
}

.slide-indicators-tela2-container {
    display: flex;
    justify-content: center;
    align-items: center;
    margin-top: 10px; 
}


.slide-indicator {
    width: 12px;
    height: 8px;
    flex-shrink: 0;
    border-radius: 16px;
    background: #FFC0B8;
    margin-right: 8px;
    z-index: 10000;
}

/* Indicador Ativo de Slide */
.active-slide {
    width: 20px;
    height: 10px;
    flex-shrink: 0;
    border-radius: 16px;
    background: var(--laranja);
    margin-right: 8px;
}

.jatemconta {
    text-align: center;
    color: #7C7C7C;
    font-family: Signika;
    font-size: 17px;
    font-style: normal;
    font-weight: 400;
    line-height: normal;
    margin-top: 20px; /* Ajuste conforme necessário */
}

.texto-entrar {
    text-align: center;
    color: var(--verde);
    font-family: Signika;
    font-size: 17px;
    font-style: normal;
    font-weight: 400;
    line-height: normal;
}

