.logo {
    display: flex;
    justify-content: center;
    align-items: center;
    color: var(--verde);
    font-size: 30px;
    margin-top: 50px;
    font-weight: 900; /* Isto é geralmente o mais grosso disponível, mas depende da fonte */
    margin-left: 17px;
}




.header {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 0 20px; /* Ajuste o padding conforme necessário */
    /* Certifique-se de que a .header esteja ocupando toda a largura da página */
}



.container-counter {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    width: 100%; /* Isso fará com que o container ocupe toda a largura possível */
    font-size: 25px;
    margin-top: 5px;
}


