.titulo-perfil-container {
    display: flex;
    justify-content: center;
    align-items: center;
    margin-top: 50px;
}

.titulo-perfil{
color:  #0D0D0D;
text-align: center;
font-size: 18px;
font-style: normal;
font-weight: 400;
letter-spacing: 0.6px;
}

.foto-container {
    display: flex;
    justify-content: center;
    align-items: center;
    margin-top: 25px;
    background: none;
}


.circle {
    position: relative;
    width: 160px;
    height: 160px;
    flex-shrink: 0;
    border-radius: 50%;
    background: none;
}



.circle img {
    width: 100%;
    height: 100%;
    object-fit: cover;
    border-radius: 50%;
}

.premium-badge {
    position: absolute;
    bottom: 0;
    right: 0;
    width: 28px;
    height: 28px;
    flex-shrink: 0;
    border-radius: 50%;
    background-color: var(--laranja);
    display: flex;
    align-items: center;
    justify-content: center;
    margin-right: 22px;
}

.premium-badge svg {
    width: 15px;
    height: auto;
    fill: white;
}

.svg2{
    margin-left: 8px;
}


.nome-container{
    display: flex;
    align-items: center;
    justify-content: center;
    margin-top: 30px;
}

.nome{
    color: #272727;
font-family: Signika;
font-size: 25px;
font-style: normal;
font-weight: 400;
line-height: normal;
letter-spacing: 1.25px;
}

.cargo-container{
    display: flex;
    align-items: center;
    justify-content: center;
    margin-top: -35px;
}

.cargo{
    color: #A1A1A1;
font-family: Signika;
font-size: 16px;
font-style: normal;
font-weight: 400;
line-height: normal;
letter-spacing: 0.8px;
}


.opcoes-container {
    width: 375px;
    height: auto;
    margin: auto;
    display: grid;
    grid-template-columns: 1fr;
    grid-template-rows: repeat(5, min-content); /* Altura mínima conforme o conteúdo */
    gap: 5px;
    margin-top: 10px;
    margin-bottom: 80px;
}

.opcao {
    display: flex;
    align-items: center;
    margin: 0;
}

.retangulo-amarelo {
    width: 48px;
    height: 48px;
    background-color: #FFF8EE;
    display: flex;
    justify-content: center;
    align-items: center;
    margin-left: 10px;
}

.texto-centralizado {
    flex: 1; /* Ocupa o restante do espaço disponível */
    display: flex;
    justify-content: center;
    align-items: center;
    color: #707070;
    font-family: "Signika";
    font-size: 17px;
    font-style: normal;
    font-weight: 400;
    line-height: normal;
    letter-spacing: 0.85px;
}

.seta-direita {
    width: 48px;
    height: 48px;
    margin-right: 10px;
    display: flex;
    justify-content: center;
    align-items: center;
    /* Adicione estilos para criar uma seta (triângulo) aqui */
}


