.suadieta {
    display: flex;
    justify-content: center;
    align-items: center;
    margin-top: 20px;
}

.SUA-DIETA {
color: var(--typography-heading-main, #0D0D0D);
text-align: center;
font-family: Abel;
font-size: 15px;
font-style: normal;
font-weight: 400;
line-height: 16px; /* 133.333% */
letter-spacing: 0.6px;
font-family: "Signika"
}

.refeicoes-dicas {

    display: flex;
    border-radius: 16px;
    justify-content: center;
    align-items: center;
    margin-top: 40px;
}


.refeicoes {
    width: 156px;
    height: 48px;
    flex-shrink: 0;
    border-radius: 16px 0px 0px 16px;
    background: #91C788;
    display: flex;
    align-items: center;
    justify-content: center;
    color: #fff;
    font-family: "Signika"
}

.dicas {
    width: 157px;
    height: 48px;
    flex-shrink: 0;
    border-radius: 0px 16px 16px 0px;
    background: #EFF7EE;
    display: flex;
    align-items: center;
    justify-content: center;
    color: #000;
    font-family: "Signika"
}

.imagem {
        display: flex;
        justify-content: center;
        align-items: center;
        margin-top: 130px;
}

.texto-dieta{
    display: flex;
    justify-content: center;
    align-items: center;
}

.textodieta{
    color: #A9A9A9;
    text-align: center;
    font-feature-settings: 'clig' off, 'liga' off;
    font-family: Signika;
    font-size: 16px;
    font-style: normal;
    font-weight: 400;
    line-height: 22.4px; /* 140% */
    letter-spacing: -0.24px;

}

.button-container{
    display: flex;
    justify-content: center;
    align-items: center;
    margin-top: 60px;
  }


  .start-button {
    width: 320px;
    height: 88px;
    border-radius: 24px;
    background: var(--laranja);
    color: #FFF;
    font-feature-settings: 'clig' off, 'liga' off;
    font-family: Signika;
    font-size: 17px;
    font-style: normal;
    font-weight: 700;
    line-height: normal;
    letter-spacing: -0.24px;
    border: none;
}

