/* Importando a fonte Signika do Google Fonts */
@import url('https://fonts.googleapis.com/css2?family=Signika:wght@300;400;600;700&display=swap');

body, html {
  margin: 0;
  padding: 0;
  font-family: 'Signika', sans-serif;
}

/* Estilos do container principal para alinhar tudo no centro */
.login-container {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  height: 100vh;
  margin: auto;
  padding: 20px;
  max-width: 400px; /* Largura máxima do container */
}

h1 {
  margin-top: -40px;
  color: var(--verde);
  margin-bottom: 32px; /* Ajuste conforme necessário */
}

.social-login{
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
}

.facebook {
  width: 324px;
  height: 58px;
  background-color: #1877F2;
  border-radius: 10px;
  display: flex;
  align-items: center;
  padding: 5px; /* Espaçamento interno para o conteúdo */
}

.facebook img {
  width: 100px; /* Ajuste o tamanho da imagem conforme necessário */
  height: 120px;
  margin-right: 10px; /* Espaçamento entre a imagem e o texto */
  margin-left: -20px;
}

.facebook p {
  color: white; /* Cor do texto */
  font-size: 16px; /* Tamanho da fonte do texto */
  margin: 0; /* Remova a margem padrão do parágrafo */
  margin-left: -5px;
}

.google img{
  width: 40px;
  margin-left: 8.75px;
}


.google p{
  margin-left: 35px;
}
.google {
  margin-top: 10px;
  width: 324px;
  height: 58px;
  background-color: white;
  border-radius: 10px;
  border: 1px solid #000; /* Borda igual em todos os lados (1px de largura e cor preta) */
  display: flex;
  align-items: center;
  padding: 5px; /* Espaçamento interno para o conteúdo */
  margin-bottom: 15px;
}


/* Separador 'OR' */
.or-separator {
  display: flex;
  align-items: center;
  color: #91C788;
  margin: 16px 0; /* Ajuste conforme necessário */
}

.or-separator hr {
  flex-grow: 1;
  border: none;
  height: 1px;
  background-color: #91C788;
  margin: 0 8px;
}

.input2-container {
  width: 100%;
  margin-top: -25px;
}

.input-with-icon {
  display: flex;
  align-items: center;
  margin-bottom: 15px;
  background-color: #f5f5f5;
  border-radius: 20px;
  padding: 10px;
  height: 30px;
  max-width: 400px;
}

.input-with-icon img {
  width: 20px;
  height: 20px;
  margin-right: 10px;
  opacity: 0.5;
}

.input-with-icon input {
  width: 100%;
  border: none;
  background-color: transparent;
  outline: none;
  font-size: 20px;
}

button {
  margin-top: 50px;
  width: 200px;
  height: 70px;
  padding: 15px;
  border-radius: 20px;
  border: none;
  background-color: #FF9385;
  color: white;
  font-size: 16px;
  cursor: pointer;
}

button:hover {
  background-color: #45a049;
}

.input-label{
  margin-left: 10px;
}

/* ... restante do seu CSS ... */

.forgot-password-link {
  display: block;
  text-align: right;
  margin-top: -5px; /* Ajuste conforme necessário para alinhar corretamente */
  margin-right: 10px; /* Ajuste conforme necessário para alinhar corretamente */
  color: var(--verde); /* Uma cor comum para links */
  font-size: 15px; /* Ou o tamanho que preferir */
  text-decoration: none; /* Remove o sublinhado padrão dos links */
}

.forgot-password-link:hover {
  text-decoration: underline; /* Adiciona sublinhado ao passar o mouse para indicar clicabilidade */
}

.jatemconta {
  text-align: center;
  color: #7C7C7C;
  font-family: Signika;
  font-size: 17px;
  font-style: normal;
  font-weight: 400;
  line-height: normal;
  margin-top: 20px; /* Ajuste conforme necessário */
}