.sidebar {
    position: fixed;
    left: 0;
    top: 0;
    width: 250px;
    height: 100%;
    background-color: rgb(255, 255, 255);
    box-shadow: 2px 0px 5px rgba(0, 0, 0, 0.2);
    z-index: 100;
    /* outras estilizações */
  }
  
  .foto-perfil{
    display: flex;
    align-items: center;
    justify-content: center;
  }

  .circle {
    width: 150px; /* Largura do círculo */
    height: 150px; /* Altura do círculo */
    background-color: blue; /* Cor de fundo do círculo */
    border-radius: 50%; /* Define o border-radius como 50% para criar um círculo */
    display: flex;
    align-items: center;
    justify-content: center;
    margin-top: 40px;
}

.circle img {
  width: 200px;
}

.email-cell {
  display: flex;
  align-items: center;
  justify-content: center;
  margin-top: -40px;
}

.info {
  color: #9EA1B1;
font-size: 14px;
font-style: normal;
font-weight: 400;
line-height: normal;
}

.icones{
  margin-top: 30px;
}

.opcao {
  padding: 10px 20px; /* Ajustei o padding para controlar o espaçamento */
  display: flex;
  flex-direction: row;
  margin-top: -30px; /* Reduzi o margin-top para aproximar as opções */
  align-items: center; /* Centraliza verticalmente o conteúdo */
}


.opcao img{
  width: 23px;
  height: 23px;
  opacity: 0.5;
}

.opcao p{
  margin-left: 15px;
  font-size: 20px;
  color: #707070;
}

.title-info-container{
  display: flex;
  align-items: center;
  justify-content: center;
}

.title-info{
  font-weight: 700;
  font-size: 30px;
  COLOR: #455A64;
}

.fechar-btn {
  position: absolute;
  top: 10px;
  right: 10px;
  cursor: pointer;
}

.fechar-btn img {
  width: 25px;
  height: 25px;
}


/* Animação de abertura */
@keyframes slideIn {
  from {
    transform: translateX(-100%);
  }
  to {
    transform: translateX(0);
  }
}

/* Animação de fechamento */
@keyframes slideOut {
  from {
    transform: translateX(0);
  }
  to {
    transform: translateX(-100%);
  }
}

.sidebar {
  animation-duration: 0.5s; /* Duração da animação */
  animation-fill-mode: both; /* Garante que a propriedade 'transform' permaneça após a conclusão da animação */
}
