.camera {
    width: 375px;
    height: 812px;
    background-color: #cccccc; 
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    position: relative; 
    margin: 0 auto; /* Centraliza horizontalmente na página */
    box-shadow: 0 0 10px rgba(0, 0, 0, 0.5); /* Adiciona sombra para melhor visibilidade em telas grandes */
  }

  .camera.sem-before::before {
    content: none; /* Remove o efeito before */
  }

  
.camera::before {
    content: "";
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background-color: rgba(0, 0, 0, 0.7); /* Cor de fundo com opacidade */
    backdrop-filter: blur(5px); /* Efeito de desfoque no conteúdo de fundo */
    z-index: 1; /* Coloque-o acima do conteúdo */
  }

.top-bar {
  display: flex;
  justify-content: space-between;
  padding: 10px;
  margin-top: 40px;
  z-index: 2; /* Coloque-o acima do pseudo-elemento */
}

.fechar{
    margin-left: 30px;
}

.flash {
    margin-right: 30px;
}


.footer {
  background-color: white;
  border-radius: 32px 32px 0 0;
  padding: 20px;
  height: 168px;
  z-index: 2; /* Coloque-o acima do pseudo-elemento */
  display: flex;
  justify-content: space-between;
  align-items: center;
  position: relative;
}

.focus{
    display: flex;
    text-align: center;
    justify-content: center;
    z-index: 3; /* Coloque-o acima de tudo */
}

.tirar-foto, .inverter-foto, .outra-foto {
    width: 40px;
    height: 40px;
    border-radius: 12px;
    background-color: #FFC0B8;
}

.tirar-foto {
    width: 72px;
    height: 72px;
    border-radius: 50%;
    background-color: #FFC0B8;
    display: flex;
    justify-content: center; /* Centraliza horizontalmente */
    align-items: center; /* Centraliza verticalmente */
}

.inverter-foto {
    margin-right: 20px;
}

.tirar-outra{
color: #91C788;
font-size: 25px;
font-style: normal;
font-weight: 700;
line-height: normal;
letter-spacing: 1.25px;
margin: auto;
}