.header-container {
  display: flex;
  justify-content: space-between;
  align-items: center;
  color: #91C788; /* Cor do texto, ajuste conforme necessário */
  padding: 10px 20px; /* Espaçamento interno, ajuste conforme necessário */
  margin-top: 40px;
}



.left-icon,
.right-icon {
  flex: 1;
  text-align: center;
}

.title {
  flex: 2; /* O título ocupa mais espaço no centro */
  text-align: center;
  font-size: 25px;
  font-weight: 600;
  line-height: 28px;
  letter-spacing:-0.24px;
  margin-right: 10px;
}

.left-icon img {

}


.right-icon img {
  width: 38px; /* Ajuste o tamanho dos ícones conforme necessário */
  height: 38px;
  border-radius: 25%; /* Isso tornará a imagem PNG circular */

}


.algum-texto-aqui{
  display: flex;
  align-items: center;
  justify-content: center;
  margin-top: -25px;
}

.algum-texto-aqui p{
  color: #7B7B7B;
text-align: center;
font-feature-settings: 'clig' off, 'liga' off;
font-size: 18px;
font-style: normal;
font-weight: 400;
line-height: 28px; /* 155.556% */
letter-spacing: -0.24px;
}

.veja-dieta-container {
  display: flex;
  justify-content: center;
  align-items: center;
  margin-top: 20px;
}

.dieta-box {
  width: 320px;
  height: 169px;
  border-radius: 32px;
  background: linear-gradient(106deg, rgba(255, 242, 240, 0.50) -103.48%, #FFF8EB 104.24%);
  padding: 20px;
  box-sizing: border-box;
  text-align: center;
}

.dieta-title {
  color: #330600;
  font-size: 17px;
  font-weight: 600;
  font-feature-settings: 'clig' off, 'liga' off;
  margin-bottom: 29px;
  margin-left: -120px;
  font-family: "Signika";
}

.ver-agora-btn {
  width: 104px;
  height: 32px;
  border-radius: 8px;
  background: #FF8473;
  color: #fff;
  display: flex;
  align-items: center;
  justify-content: center;
  cursor: pointer;
  font-family: "Signika";
  margin-left: 12px;
}

.hamburguer-img {
  width: 111.428px;
  height: 120.73px;
  flex-shrink: 0;
  margin-left: 180px;
  margin-top: -130px; /* Experimente um valor negativo menor */
}

.dieta-title span {
margin-left: -83px;
}

.slide-indicators-container {
  display: flex;
  justify-content: center;
  align-items: center;
  margin-top: 20px; /* Ajuste conforme necessário */
}

.slide-indicator {
  width: 12px;
  height: 8px;
  flex-shrink: 0;
  border-radius: 16px;
  background: #FFC0B8;
  margin-right: 8px; /* Espaçamento entre os indicadores */
  z-index: 1;
}

/* Indicador Ativo de Slide */
.active-slide {
  width: 20px;
  height: 10px;
  flex-shrink: 0;
  border-radius: 16px;
  background: #FF8473;
  margin-right: 8px; /* Espaçamento entre os indicadores */
  z-index: 1;
}

.retangulo-container {
