.box {
  width: 320px;
  height: 80px;
  border-radius: 24px;
  background: rgba(27, 87, 124, 0.71);
  padding: 20px;
  box-sizing: border-box;
  display: flex; /* Updated to flex to align items in a row */
  align-items: center; /* Center items vertically */
  justify-content: flex-start; /* Align items to the start of the box */
  text-align: left; /* Align text to the left */
  margin: 0 auto; /* Center horizontally */
}

