.icon-container {
  display: flex;
  justify-content: space-around; /* Ajusta o espaçamento entre os ícones */
  align-items: center;
  padding: 10px;
  position: fixed;
  bottom: 0;
  left: 0;
  width: 100%;
  background-color: white;
}
  
.icon-container img {
  width: 30px; /* Ajusta o tamanho dos ícones */
  height: 30px;
  margin: 0 6px; /* Ajusta a margem entre os ícones */
  color: #999999;
}


/* Adicione classes para os ícones que deseja ajustar a margem */
.icon-container .green-circle img {
  margin: 0 6px; /* Ajuste a margem para os ícones dentro da bolinha verde */
}

.icon-container .outro-icone {
  margin: 0 6px; /* Ajuste a margem para outros ícones aqui */
}

.icon-container .teste {
  margin: 0 20px; /* Ajuste a margem para o ícone com a classe "teste" */
}



  .green-circle {
    width: 50px;
    height: 50px;
    flex-shrink: 0;
    background-color: #91C788; /* Cor da bolinha verde */
    border-radius: 50%;
    display: flex;
    justify-content: center;
    align-items: center;
  }
  
  .green-circle img{
    width: 20px;
    height: 25px;
  }
  
  .teste{
    margin-right: 51px;
  }