/* src/components/Preloader.css */
.preloader-container {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    height: 100vh;
    background-color: var(--verde);
}

.preloader-logo {
    max-width: 200px;
    height: auto;
    margin-bottom: 70px;
}

.preloader-spinner {
    border: 8px solid #ffffff; /* Cor da borda da bolinha de loader */
    border-top: 8px solid var(--laranja); /* Cor da parte superior da bolinha de loader */
    border-radius: 50%;
    width: 50px;
    height: 50px;
    animation: spin 1s linear infinite; /* Animação de rotação */
    margin-top: 20px; /* Espaço entre a logo e a bolinha de loader */
}

@keyframes spin {
    0% { transform: rotate(0deg); }
    100% { transform: rotate(360deg); }
}
