.logo {
    display: flex;
    justify-content: center;
    align-items: center;
    color: var(--verde);
    font-size: 30px;
    margin-top: 50px;
    font-weight: 900; /* Isto é geralmente o mais grosso disponível, mas depende da fonte */
    margin-left: 17px;
}





.header {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 0 20px; /* Ajuste o padding conforme necessário */
    font-weight: 200;
}

.center{
  display: flex;
  align-items: center;
  justify-content: center;
}


.container-counter {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    width: 100%; /* Isso fará com que o container ocupe toda a largura possível */
    font-size: 25px;
    margin-top: 5px;
}


.desc{
    margin-top: -20 px;
    margin-left: 20px;
    color: #969292;
}

.desc3{
  margin-top: -20px;
  margin-left: 20px;
  color: #969292;
}
.inputs-container {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  height: 100vh; /* Centraliza verticalmente */
  text-align: center; /* Centraliza horizontalmente */
  margin-top: -140px;
}

.input-group {
  margin-bottom: 20px;
}

.input-label {
  font-size: 14px;
  color: #333;
  margin-bottom: 5px;
  display: block;
}

.input33-label {
  font-size: 14px;
  color: #333;
  margin-bottom: 10px;
  margin-left: -220px;
  display: block;
}

.input333-label {
  font-size: 14px;
  color: #333;
  margin-bottom: 10px;
  margin-left: -160px;
  display: block;
}

.input-wrapper {
  border: 1px solid #ccc; /* Adiciona uma borda fina cinza */
  border-radius: 5px;
  padding: 10px;
  background-color: #fff; /* Define o fundo como branco */
  width: 300px; /* Defina a largura desejada */
}

.input-field {
  width: 100%; /* Para preencher completamente o espaço dentro do input-wrapper */
  border: none;
  padding: 8px;
  outline: none;
}

.custom-button {
  background-color: var(--laranja);
  color: #fff;
  border: none;
  padding: 10px 20px;
  border-radius: 5px;
  cursor: pointer;
  font-size: 16px;
  margin-top: -20px; /* Espaçamento superior para o botão */
}
